.box{
  max-width: 250px;
  min-height: 350px;
  align-items: center;
  justify-content: space-between;
   margin: 120px auto;
  padding:0px 20px;
  background-color: #8CA6C4;
  border-bottom: 1px solid #333;
  border-radius: 5px;
  
}
.inputData{
  text-align: center;
  border: none;
}
.inputField{
  margin-top: 5%;
  border-radius: 30px;
  height:30px;
  border: none;
  
}
.inputField:focus{
  border: none;
  
}
.info{
  text-align: center;
  margin-top: 40%;
  overflow-x: hidden;
}
